.card-wrap {
  display: inline-flex;
  grid-template-columns: repeat(auto-fit, minmax(30rem, 16rem));
  gap: 2rem;
  justify-content: center;
  flex-direction: column;
  justify-content: space-between;
}
.cardcontent {
  padding: 0.8rem;
  overflow: hidden;
  box-shadow: 0 1px 2px gray;
  margin: 2%;
  border-radius: 0.2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  cursor: auto;
  transition: transform 200ms ease-in;
  min-width: min-content;
}

.cardcontent:hover {
  transform: scale(1.02);
  background: rgb(182, 255, 245);
  color: black;
  border: 2px solid rgb(182, 255, 245);
}

.card-content-footer {
  display: inline-flex;
}

.provider {
  height: 10%;
  width: 18%;
  object-fit: auto;
  margin-left: 20%;
  margin-top: 10%;
}
.check-coverage {
  font-size: 70%;
  margin-left: 16%;
  height: 5%;
  margin-top: 8%;
  background-color: rgb(182, 255, 206);
  border-style: none;
  padding: 2%;
  border-radius: 2%;
  cursor: pointer;
}

.check-coverage:hover {
  transform: scale(1.2);
  background-color: navajowhite;
}
